export default {
	data() {
		return {
			uid:'',
			token:'',
			data:'',
			qrcode:''
		}
	},
	mounted(){
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getInfo()
			this.getPromoteQrcode()
		}
	},
	methods: {
		to(where){
			uni.navigateTo({
				url:`./${where}`
			})
		},
		getInfo(){
			var that=this
			var url='&r=api.member.agent'
			var params={
				uid:this.uid,
				token:this.token
			}
			  this.$apiPost(url,params).then((res) =>{
				that.data=res.data	
			  })
		},
		getPromoteQrcode(){
			var that=this
			var url='&r=api.member.agent.invited_qrcode'
			var params={
				uid:this.uid,
				token:this.token
			}
			  this.$apiPost(url,params).then((res) =>{
				that.qrcode=res.data.qrcode	
			  })
		},
		showQrcode(){
			uni.previewImage({
				current:this.qrcode,
				urls:[this.qrcode]
			})
		}
	}
}
